* {
    padding: 0%;
    margin: 0%;
}

body {
    margin-top: 5rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    background-color: lightblue;
}

.game {
    margin-top: 4rem;
    height: 60vmin;
    width: 60vmin;
    gap: 1rem;
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}

.box {
    background-color: black;
    width: 18vmin;
    height: 18vmin;
    border: none;
    box-shadow: 0 4px 8px 0 red, 0 6px 20px 0 black;
    font-size: 100px;
    font-weight: bold;
    border-radius: 25px;
}
h1{
    color: black;
    font-size: 100px;
    font-weight: 900;
    text-shadow:2px 1px red ;
}
h1{
    font-size: 25px;
}
.red{
    color: red;
}
.green{
    color: goldenrod;
}

.resetBtn {
    margin-top: 2rem;
    width: 30vmin;
    height: 10vmin;
    font-size: 2rem;
    border: none;
    border-radius: 1rem;
    background-color: black;
    color: lightblue;
    margin-bottom: 5rem;
}

.restartBtn {
    display: none;
    width: 30vmin;
    height: 10vmin;
    font-size: 2rem;
    border: none;
    border-radius: 1rem;
    background-color: black;
    color: lightblue;
    margin-bottom: 45rem;
    margin-top: 2rem;
}

.wincon {
    display: none;
    margin-top: 20rem;
    /* display: flex; */
    /* flex-direction: column; */
    align-items: center;
    justify-content: space-around;
}

.winner {
    color: black;
    font-size: 25PX;
}


/* .winVisible {
    display: block;
} */

@media only screen and (max-width: 768px) {
    .game {
        height: 90vmin;
        width: 80vmin;
    }
    .wincon{
        width: 80vmin;
    }
    .winner{
        font-size: 2.7rem;
    }
}